






























import { Vue, Component, Prop } from "vue-property-decorator";

@Component
export default class modalConfCreation extends Vue{

    login: string ='';
    confName: string = '';

    modalClosed(): void {
        if(!this.login || !this.confName) return;
        this.$emit('conf-created', { login: this.login, confName: this.confName });
    }
}
